.form-wrapper {
    .form-header {
        margin-top: 0;
        text-align: center;
        text-shadow: 0 0 1px #555555;
    }

    .form-link a {
        color: #444444;

        &:hover, &:focus, &:active {
            color: #1ab394;
        }
    }

    textarea {
        resize: none;
    }
}
